.ct-player {
    background-color: #ddd;
    height: 80px;
    border-radius: 7px;
    display: flex;
    flex-flow: row;
    justify-content: left;
    align-items: center;
}

.playbtn {
    color: orangered;
    height: 38px;
    width: 38px;
}
.sb-soundplayer-icon {
    height: 20px;
    width: 20px;
    -webkit-transform: translateX(-3px);
            transform: translateX(-3px);
}
.icon-volume {
    border: none;
    color: orangered;
    height: 38px;
    width: 38px;
}
.sb-soundplayer-volume-range {
    display: flex;
    flex-flow: row;
    align-items: center;
}
.sb-soundplayer-volume-range[type=range] {
    -webkit-appearance: none;
    -moz-appearance: none;
         appearance: none;
    width: 98%;
    height: 5px;
    background-color: rgb(196, 196, 196);
    border-radius: 2px;
    cursor: pointer;
}
.sb-soundplayer-volume-range[type=range]::-webkit-slider-thumb {
    -webkit-appearance: none;
    appearance: none;
    width: 15px;
    height: 15px;
    background-color: orangered;
    border-radius: 50px;
    cursor: pointer;

}
.sb-soundplayer-volume-range {
    border-radius: 50%;
    color: orangered;
}

.title-player {
    color: orangered;
    text-align: center;
}
.close-player {
    -webkit-transform: translate(50%,-100%);
            transform: translate(50%,-100%);
    cursor: pointer;
    font-size: 25px;
}
.close-player:hover {
    color: orangered;
    
}
